import URLs from './URLs'
import axios from 'axios'

const appkey = 'lzl906790326_1585638309415'
const instant = axios.create({
    baseURL : URLs.baseURL,
    params : {
        appkey,
    }
})

const searchHotWords = _ => {
   return instant.get(URLs.searchHotWords)
}
// function searchHotWords (data){
//     return instant.get(URLs.searchHotWords)
// }

const search = _ => {
    return instant.get(URLs.search)
}
/**
 * 首页左侧导航
 * @param {*} _ 
 */
const getNavList = _ => {
    return instant.get(URLs.firstPageNav)
} 

const getkeyWords = _ => {
    return instant.get(URLs.keyWords)
}

const getHotCity = _ => {
    return instant.get(URLs.hotCity);
}

const getRecentCity = _ => {
    return instant.get(URLs.recentCity)
}

const getProvinceList = _ => {
    return instant.get(URLs.getProvince);
}

const getPosition = _ => {
    return instant.get(URLs.getPosition)
}

const getCityList = _ => {
    return instant.get(URLs.getCityList)
}
const Login = data => {
    return instant.get(URLs.Login,{
        params:{
            ...data,
        }
    })
}
const register = data => {
    return instant.get(URLs.register,{
        params : {
            ...data
        }
    })
}
const getProductsList = _ => {
    return instant.get(URLs.goodsList)
}
export default {
    searchHotWords,
    search,
    getNavList,
    getkeyWords,
    getHotCity,
    getRecentCity,
    getProvinceList,
    getPosition,
    getCityList,
    Login,
    register,
    getProductsList
}
