<template>
  <div class="m-istyle">
    <dl @mouseover="over" :class="nav.class">
      <dt>{{ nav.title }}</dt>
      <dd
        v-for="item in nav.list"
        :key="item.text"
        :class="{ active: kind == item.tab }"
        :data-type="item.tab"
      >
        {{ item.text }}
      </dd>
    </dl>
    <ul class="ibody">
      <li v-for="(item, index) in list[kind]" :key="index">
        <el-card :body-style="{ padding: '0px' }" shadow="never">
          <img :src="item.image" class="image" />
          <div class="cbody">
            <div class="title" :title="item.title">{{ item.title }}</div>
            <div class="sub-title" :title="item.sub_title">
              {{ item.sub_title }}
            </div>
            <div
              class="price-info"
            >
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">{{
                  item.price
                }}</span>
              </span>
              <span class="old-price"
                >门市价¥{{ item.price }}</span
              >
              <span class="sold bottom-right-info">{{ item.address }}</span>
            </div>
            <!-- <div class="price-info" v-else-if="!item.rentNum">
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">抢光了</span>
              </span>
            </div>
            <div class="price-info" v-else>
              <span class="current-price-wrapper">
                <span class="price-symbol numfont">¥</span>
                <span class="current-price numfont">{{
                  item.price_info.avg_price
                }}</span>
                <span class="units">/{{ item.price_info.units }}人均</span>
              </span>
            </div> -->
          </div>
        </el-card>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      kind: "all",
      
      list: [],
    };
  },
  async created(){
    const result = await this.$api.getkeyWords()
    this.list = result.data.data;
  },
  props: ["nav"],
  methods: {
   async over(e) {
      let dom = e.target;
      let domName = dom.tagName;
      if (domName != "DD") {
        return false;
      }
      this.kind = dom.getAttribute("data-type");
      //根据不同的页面请求数据
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>
