<template>
  <div class="page-changeCity">
      <el-row>
          <el-col>
              <province />
          </el-col>
      </el-row>
      <el-row>
          <el-col>
              <hot title="热门城市 :" :cityList="hotCityList"/>
          </el-col>
      </el-row>
      <el-row>
          <el-col>
              <hot title="最近访问 :" :cityList="currentCityList"/>
          </el-col>
      </el-row>
      <el-row>
          <el-col>
              <categroy />
          </el-col>
      </el-row>
  </div>
</template>

<script>
import province from '@/components/changeCity/province'
import hot from '@/components/changeCity/hot'
import categroy from '@/components/changeCity/categroy'
export default {
    components : {
        province,
        hot,
        categroy
    },
    data(){
        return {
            hotCityList : [],
            currentCityList : []
        }
    },
    async created(){
      const result = await this.$api.getHotCity();
      this.hotCityList = result.data.data.map(item => {
          return item.name;
      });
      
      const res = await this.$api.getRecentCity();
      this.currentCityList = res.data.data.map(item => {
          return item.name;
      })

    }
}
</script>

