<template>
  <div
    class="choose-wrap"
    :class="['choose-wrap', disabled ? 'disabled' : '']"
    @click="handlerClick"
    v-document-click="documentClick"
  >
    <div class="choose">
      <span>{{ value }}</span>
      <i class="el-icon-caret-bottom"></i>

      <!-- 下拉框内容 -->
      <div class="mt-content" :class="{ active: isActive }">
        <h2>{{ title }}</h2>
        <div :class="['wrapper',className]">
          <div class="col" v-for="(col, index) in renderList" :key="index">
            <span
              class="mt-item"
              :class="{ active: item.name === value }"
              v-for="(item, index) in col"
              :key="index"
              @click="changeValue(item)"
              >{{ item.name }}</span
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["list", "title", "value", "isActive", "disabled",'className'],
  data() {
    return {};
  },
  computed: {
    renderList() {
      let col = Math.ceil(this.list.length / 12);
      let listData = [];
      for (let i = 0; i < col; i++) {
        let colData = this.list.slice(i * 12, i * 12 + 12);
        listData.push(colData);
      }
      return listData;
    },
  },
  methods: {
    handlerClick(e) {
      e.stopPropagation();
      if (!this.disabled) {
        this.$emit("changeActive", true);
      }
    },
    documentClick() {
      this.$emit("changeActive", false);
    },
    changeValue(val) {
      this.$emit("change", val);
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/changecity/select.scss";
</style>