<template>
  <div class="categroy">
    <!-- 按拼音选择，上部分 -->
    <dl class="m-categroy">
      <dt>按拼音首字母选择：</dt>
      <dd v-for="(item, index) in cityGroup" :key="index">
        <a :href="'#city-' + item">{{ index }} </a>
      </dd>
    </dl>
    <!-- 按详细的拼音选择  -->
    <dl class="m-categroy-section"  v-for="(city, index) in cityGroup" :key="index" :id="'city-'+ index">
      <dt>{{index}}</dt>
      <dd>
        <span v-for="item in city" :key="item.id" @click="changeCity(item)">{{ item.name }}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // alphabet: "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split(""),
      // alphabet : [],
      cityGroup : {},
    };
  },
  async created() {
    let data = [];
    const result = await this.$api.getCityList();
    var obj = {};
    result.data.data.forEach(item => {
      //数组去重的方式将得到的数据根据不同的首字母编排   比如: {G ：'广州',S ：'深圳'}
        if(!obj[item.firstChar.toUpperCase()]){
            obj[item.firstChar.toUpperCase()] = [];
        }
        obj[item.firstChar.toUpperCase()].push(item);
    })
        this.cityGroup = obj;
  },
  methods : {
    changeCity(val){
      this.$store.dispatch('setPosition',val); 
      this.$router.push({name : 'Index'});
    }
  }
};
</script>

<style lang='scss'>
    @import "@/assets/css/changecity/categroy.scss";
</style>