<template>
  <div class="m-geo">
    <div class="position">
      <i class="el-icon-location" />
      {{$store.state.position.name}}
      <router-link class="changeCity" :to="{ name: 'changeCity' }"> 
          切换城市 
      </router-link>
         [ 
         <a href="#" v-for="(item,index) in nearCityList" :key="index">{{item.name}}</a>
         <!-- <a href="#">门头沟区</a>
         <a href="#">大厂回族自治县</a>
         <a href="#">廊坊</a> -->
         ]
    </div>
    <div class="m-user" v-if="!$store.state.userName">
      <router-link class="login" :to="{ name: 'login' }"> 立即登录 </router-link>
      <router-link class="register" :to="{ name: 'register' }"> 注册 </router-link>
    </div>
  </div>
</template>

<script>
export default {
 async created(){
    const value =  await this.$api.getPosition();
    this.$store.dispatch('setPosition',value.data.data)
    this.nearCityList = value.data.data.nearCity;
  },
  data(){
    return {
      nearCityList : []
    }
  },
  watch : {
    '$store.state.position.name' : function(){
      this.nearCityList = this.$store.state.position.nearCity;
    }
  }
};
</script>

