<template>
  <div class="m-menu">
    <dl class="nav" @mouseleave="leave">
      <dt>全部分类</dt>
      <dd
        v-for="(item, index) in curList"
        :key="index"
        @mouseenter="enter(item)"
      >
        <i :class="item.type"></i>
        {{ item.name }}
        <span class="arrow"></span>
      </dd>
    </dl>

    <div class="detail" v-if="curDetail" @mouseenter="enterDetail" @mouseleave="leaveDetail">
      <template v-for="(item, index) in curDetail.items">
        <h4 :key="item + '_' + index">{{ item.title }}</h4>
        <span v-for="(i, index) in item.items" :key="i + '_' + index">{{
          i
        }}</span>
      </template>
    </div>
  </div>
</template>

