<template>
  <div>
    <span class="name">按省份选择:</span>
    <m-select
      :list="provinceList"
      title="省份"
      :value="province"
      @changeActive="changeProvinceActive"
      :isActive="provinceActive"
      @change = "changeProvince"
      className ="province"
    />
    <m-select
      :list="cityList"
      title="城市"
      :value="city"
      @changeActive="changeCityActive"
      :isActive="cityActive"
       @change = "changeCity"
       :disabled ="cityDisable"
       className="city"
    />
  </div>
</template>

<script>
import MSelect from "./select";
export default {
  components: {
    MSelect,
  },
 async created(){
    const result = await this.$api.getProvinceList();
    this.provinceList = result.data.data.map(item => {
      item.name = item.provinceName;
      return item;
    })
  },
  data() {
    return {
      provinceList: [
  
      ],
      province: "省份",
      cityList: [

      ],
      city: "城市",
      provinceActive: false,
      cityActive: false,
      cityDisable : true,
    };
  },
  methods: {
    changeProvinceActive(flag) {
      this.provinceActive = flag;
      if (flag) {
        this.cityActive = false;
      }
    },
    changeCityActive(flag) {
      this.cityActive = flag;
      if (flag) {
        this.provinceActive = false;
      }
    },
    changeProvince(value){
        this.province = value.provinceName;
        this.cityList = value.cityInfoList;
        this.cityDisable = false;
    },
   async changeCity(value){
        this.city = value.name;
        // 改变左上角坐标的位置
        this.$store.dispatch('setPosition',value)
        //跳转到首页
        this.$router.push({name : 'Index'})
    }
  },

};
</script>

<style lang="scss">
@import "@/assets/css/changecity/iselect.scss";
</style>