<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <img
          src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png"
          alt="美团"
        />
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <el-input
            v-model="searchWord"
            placeholder="搜索商家或地点"
            @focus="focus"
            @blur="blur"
            @input="input"
          ></el-input>
          <el-button type="primary" icon="el-icon-search"></el-button>
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
            <dd v-for="(item, index) in hotPlaceList" :key="item + '_'+ index">
              <router-link :to="{name: 'goods', params: {name: item}}">{{ item }}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="isSearchList">
            <dd v-for="(item, index) in searchList" :key="index">
              <router-link :to="{name: 'goods', params: {name: item}}" >{{ item }}</router-link>
            </dd>
          </dl>
        </div>
        <p class="suggest">
          <router-link v-for="(item, index) in suggestList" :key="item + '~' + index" :to="{name: 'goods', params: {name: item}}">{{ item }}</router-link>
          <!-- <router-link to="/">北京故宫博物院</router-link>
          <router-link to="/"> 北京欢乐谷</router-link>
          <router-link to="/"> 尚隐·泉都市生活馆</router-link>
          <router-link to="/">故宫珍宝馆</router-link>
          <router-link to="/">北京连升商务酒店</router-link>
          <router-link to="/"> </router-link> -->
        </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchWord: "",
      isFocus: false,
      hotPlaceList: [],
      searchList: [],
      suggestList: [],
    };
  },
 async created(){
    const result = await this.$api.searchHotWords();
    this.hotPlaceList = result.data.data;
    this.suggestList = result.data.data;
  },
  computed: {
    isHotPlace() {
      //当前为聚焦状态 并且  当前输入框没有值
      return this.isFocus && !this.searchWord;
    },
    isSearchList() {
      return this.isFocus && this.searchWord;
    },
  },
  methods: {
    focus() {
      this.isFocus = true;
    },
    blur() {
      setTimeout(() => {
        this.isFocus = false;
      },200)
    },
   async input(){
      let value = this.searchWord;
       const result = await this.$api.search();
       const wordList = result.data.data.list;
       let word = wordList.filter(item => {
         return item.indexOf(value) > -1;
       })
       this.searchList = word;
    }
  },
};
</script>

<style lang="scss">
@import "@/assets/css/public/header/index.scss";
@import "@/assets/css/public/header/search.scss";
</style>
