<template>
  <div class="page-login">
    <div class="login-header">
      <a class="logo" href="http://www.meituan.com"></a>
    </div>
    <div class="login-panel">
      <!-- 左边图片 -->
      <div class="banner">
        <img
          src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg"
          width="480"
          height="370"
          alt="美团网"
        />
      </div>
      <!-- 登录注册页面 -->
      <div class="form">
        <h4 v-if="error" class="tips">{{ error }}</h4>
        <p>
          <span> 账号登陆 </span>
        </p>
        <el-input
          v-model="userName"
          placeholder="手机号/用户名/邮箱"
          prefix-icon="profile"
        ></el-input>
        <el-input
          v-model="password"
          type="password"
          placeholder="密码"
          prefix-icon="password"
        ></el-input>
        <p class="foot">
          <a href="#">忘记密码?</a>
        </p>
        <el-button type="primary" class="btn-login" @click="submit"
          >登录</el-button
        >
        <div class="reg">
          <span>还没有账号？</span>
          <router-link :to="{ name: 'register' }">免费注册</router-link>
        </div>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title">用合作网站账号登录</span>
          </h3>
          <div class="oauth cf">
            <a
              class="oauth__link oauth__link--qq"
              href="/account/connect/tencent"
              target="_blank"
            ></a>
            <a
              class="oauth__link oauth__link--weibo"
              href="/account/connect/sina"
              target="_blank"
            ></a>
          </div>
        </div>
      </div>
    </div>
    <!-- footer区域 -->
    <footer>
      <ul>
        <li><a href="#">关于美团</a></li>
        <li><a href="">加入我们</a></li>
        <li><a href="">商家入驻</a></li>
        <li><a href="">帮助中心</a></li>
        <li><a href="">美团手机版</a></li>
      </ul>
      <p>
        ©2018 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号
      </p>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userName: "",
      password: "",
      error: "",
    };
  },
  methods: {
    async submit() {
      if (!this.userName) {
        this.error = "请输入账号";
        return false;
      }
      if (!this.password) {
        this.error = "请输入密码";
        return false;
      }
      const result = await this.$api.Login({
        userName: this.userName,
        password: this.password,
      });

      const { msg, status } = result.data;
      if (status == "success") {
        //登录成功 跳转页面
        this.$router.push({ name: "Index" });
        //切换用户名
        this.$store.commit('setUserName',this.userName)
      } else {
        this.error = msg;
      }
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/css/login/index.scss";
</style>